<script>
  import axios from 'axios';
  import TecladoTextoPorPasos from "@/components/TecladoTextoPorPasos.vue";

  export default 
  {
    components:
    {
      TecladoTextoPorPasos
    },
   
    data () {
        return {
          Nombre: '',
          ErrorIngresoDatos: false,
          MensajeError: "Sin error",

        }
    },
    computed: {
      Turno () 
      {
        return this.$store.state.Turno;
      },
      ConfigTurnero () 
      {
        return this.$store.state.ConfigTurnero;
      }
    },
    mounted: function() 
    {

          console.log('Validacion Nombre: ' + this.$store.state.Turno.Nombre + '');
          if (this.$store.state.Turno.Nombre + '' != '')
          {
            this.Nombre =this.$store.state.Turno.Nombre;
            console.log('No Consulta Nombre: ' + this.$store.state.Turno.Nombre + '');
            console.log('Nombre: ' + this.Nombre);
            return;
          }
          console.log('Consulta Nombre: ' + this.$store.state.Turno.Nombre + '');
          axios.get('../obtenernombreusuario.php?tipoDocumento='+this.$store.state.Turno.TipoIdentificacion +'&numeroIdentificacion='+this.$store.state.Turno.NumeroIdentificacion).then((response) => 
          {
            this.paciente =response.data; 
           //console.log(this.paciente);
            if (this.paciente.Nombre != null)
            {
              this.Nombre =this.paciente.Nombre;
              this.$store.commit("changeTurnoNombre", this.paciente.Nombre);
              this.$store.commit("changeTurnoEdad",  this.paciente.Edad);                              

            }

          });
    },
    methods: 
    {    
      redirect(parameter) 
      {
        this.$router.push(parameter);
      },
      continuar() 
      {
        if (this.$store.state.ConfigTurnero.TieneEdad == true)
        {
          this.redirect(`/IngresoEdad`); 
          return;
        }

        if (this.$store.state.ConfigTurnero.TieneCategorias == true)
        {
          this.redirect(`/SeleccionCategoria`); 
          return;
        }

        if (this.$store.state.ConfigTurnero.TienePrioridad == true)
        {
          this.redirect(`/SeleccionPrioridad`); 
          return;
        }

        this.redirect(`/GenerarTurno`); 
 
      },


     async TeclaPresionadaNombre(tecla) 
      {
        if (tecla == 0)
        {
          this.Nombre= this.Nombre.substring(0, this.Nombre.length-1); 
          return;
        }

       if (tecla == 9)
        {
          this.Nombre= this.Nombre + ' '; 
          return;
        }
             
        if (tecla == 99) 
        {
 
          if (this.Nombre.trim() == "")
          {
              this.ErrorIngresoDatos=true;
              this.MensajeError = this.$store.state.ConfigTurnero.MesajeSinNombre;
              return;
          }
          this.$store.commit("changeTurnoNombre", this.Nombre);
          this.continuar();
          return;
        }
        
        
        this.Nombre= this.Nombre + tecla;

      
      },
    },
  }

  
</script>

