<template>
  <div class="ingresoNombreModelo" @keyup.space="squack('left button clicked')">
   
    <v-container >
    <div style="height:15px;"></div>
    <v-card dark color="primary"  flat class="div-2" >
      <v-list-item-title   class="textoaunamediano pt-3  pl-3">
