      </v-list-item-title>

      <v-card light  color="secondary" width="100%"
        class="d-flex align-content-space-around  justify-space-around flex-wrap"
        
        flat
        tile
        height="200"
      >


          <div v-for="tipoDocumento in ConfigTurnero.TiposDocumento" v-bind:key="tipoDocumento.Codigo" >
            <v-btn   height="40" width="430"  elevation="0"   plain  :color="tipoDocumento.Color" block x-large rounded  :class="tipoDocumento.Clase"   
            v-on:click="clickTipoDocumento(tipoDocumento)">
              <p><b class='textoaunamediano'> {{ tipoDocumento.Nombre }}</b></p>
            </v-btn>
          </div>

      </v-card>

    </v-card>    
    <div style="height:20px;"></div>

    <v-card dark color="primary"  v-if="TipoIdentifiacionSeleccionado==true" flat class="div-2" elevation="2">
      <v-list-item-title  class="textoaunamediano pt-3 pl-3">
            <p v-html="ConfigTurnero.MensajeDatosPersonales"></p>
      </v-list-item-title>
      <v-card light  color="secondary" class=" pa-4" flat tile height="80" >    

        <v-row  justify="center">
          <v-col  cols="3" v-if="TipoIdentifiacionSeleccionado==true">
            <v-text-field color="primary" background-color="white" light   rounded  v-on:focus="ClickEnCajaDeTexto('IDENTIFICACION')" filled   outlined placeholder="Identificacin" v-model="NumeroDocumento" class="textoaunamediano text-primary pa-0 "  ></v-text-field>        
          </v-col>
          <v-col cols="7" v-if="IdentifiacionSeleccionado==true">
            <v-text-field  background-color="white" color="primary" light rounded  v-on:focus="ClickEnCajaDeTexto('NOMBRE')" filled  placeholder="Nombre Completo" outlined v-model="NombreCompleto"  class="textoaunamediano ml-5 text-primary" ></v-text-field>        
          </v-col>
          <v-col cols="2" v-if="NombreSeleccionado==true">
            <v-text-field  background-color="white"  light rounded v-on:focus="ClickEnCajaDeTexto('EDAD')" filled  outlined placeholder="Edad" v-model="Edad"  class="textoaunamediano ml-5 text-primary"   ></v-text-field>        
          </v-col>
        </v-row>
      </v-card>
    </v-card>
      <div v-if="MostrarTecladoIdentificacion" >
          <TecladoNumerico  @onKeyPress="TeclaPresionadaIdentificacion"></TecladoNumerico>
      </div>

      <div v-if="MostrarTecladoNombre" >
          <TecladoTexto  @onKeyPress="TeclaPresionadaNombre"></TecladoTexto>
      </div>

      <div v-if="MostrarTecladoEdad">
          <TecladoEdad  @onKeyPress="TeclaPresionadaEdad"></TecladoEdad>
      </div>
    <div style="height:20px;"></div>
     <v-card dark color="primary"  class="div-2" v-if="MostrarTecladoIdentificacion==false && MostrarTecladoNombre==false && MostrarTecladoEdad==false &&  EdadSeleccionado==true" >
        <v-list-item-title  class="textoaunamediano  pt-3 pl-3">
          <p v-html="ConfigTurnero.MensajeCategorias"></p>
           

        </v-list-item-title>
        <v-card light
          class="d-flex align-content-space-around  justify-space-around flex-wrap"
          color="secondary"
          flat
          tile
          height="130"
        >
        <div v-for="categoria in ConfigTurnero.Categorias" v-bind:key="categoria.Codigo" class="my-2">
          <v-btn  height="40" width="270" block x-large rounded  :color="categoria.Color" plain light   :class="categoria.Clase" v-on:click="clickCategoria(categoria)">
             <p class='textoaunamediano'><b> {{ categoria.Nombre }}</b></p>
          </v-btn>
        </div>

        </v-card>
        </v-card>  
        <v-card light  v-if="MostrarTecladoIdentificacion==false && MostrarTecladoNombre==false && MostrarTecladoEdad==false "
          class="d-flex align-content-space-around  justify-space-around flex-wrap"
          
          flat
          tile
          height="150"
        >  

          <v-row>
            <v-col cols="3"/>
            <v-col cols="6">

              <v-dialog v-if="CategoriaSeleccionado==true" transition="dialog-bottom-transition"  max-width="600" >
                <template v-slot:activator="{ on, attrs }">
                    <v-btn height="50"  v-bind="attrs" v-on="on" elevation="8" block x-large  color="primary" dark class = "headline mb-1 rounded-lg pt-5" v-on:click="Continuar()">
                      <p class='textoauna'><b> CONTINUAR</b></p>
                    </v-btn>
                </template>
                <template v-slot:default="dialog">

                  <v-card color="primary dark">
                    <v-toolbar
                      color="secondary"
                      dark
                      elevation="0"
                      class="textoauna pa-0"
                    >Atencin</v-toolbar>
                    <v-card-text  >
                      <div>
                          <p class="textoauna  pa-12 text-popup" v-html="MensajeError"></p>
                      </div>
                    </v-card-text>
                    <v-card-actions class="justify-center">
                      <v-btn height="60" width="250"
                        color="white"
                        light
                        class="textoauna pa-0 text-boton"
                        
                        @click="dialog.value = false"
                      >Cerrar</v-btn>
                    </v-card-actions>
                  </v-card>

                </template>
              </v-dialog>


    
            </v-col>
            <v-col cols="3"/>
          </v-row>
       
        </v-card>
            <div style="height:50px;"></div>   

    </v-container>  
  </div>
</template>

<style>
