<script>
  import axios from 'axios';
  import TecladoTexto from "@/components/TecladoTexto.vue";
  import TecladoNumerico from "@/components/TecladoNumerico.vue";
  import TecladoEdad from "@/components/TecladoEdad.vue";

  export default 
  {
  
    components:
    {
      TecladoTexto,
      TecladoNumerico,
      TecladoEdad

    },
    computed: 
    {
      Turno () 
      {
        return this.$store.state.Turno;
      },
      ConfigTurnero () 
      {
        return this.$store.state.ConfigTurnero;
      }
    },
    data () 
    {
        return {
          MensajeError: '',
          NombreCompleto: this.$store.state.Turno.Nombre,
          NumeroDocumento: this.$store.state.Turno.NumeroIdentificacion,
          TipoDocumento:  this.$store.state.Turno.TipoIdentificacion,
          Categoria:this.$store.state.Turno.Categoria,
          Edad:this.$store.state.Turno.Edad,
          TipoEdad:this.$store.state.Turno.TipoEdad,
          ColorIdentificacion:'',
          ColorNombre:'',
          ColorEdad:'',
          MostrarTecladoIdentificacion: false,
          MostrarTecladoNombre: false,
          MostrarTecladoEdad: false,
          ActivarInconsistencia:'on',
          TipoIdentifiacionSeleccionado:true,
          IdentifiacionSeleccionado:true,
          NombreSeleccionado:true,
          EdadSeleccionado:true,
          CategoriaSeleccionado:true,


        }
    },
    mounted: function() 
    {

      //console.log(this.$store.state.Prueba);
      this.$store.dispatch("changePrueba", "Pryueba Nueva");
      //console.log(this.$store.state.Prueba);
      console.log(this.$store.state.ConfigTurnero);
      console.log(this.$store.state.Turno);
      var vm = this;
      window.addEventListener('keyup', function(event) 
      {
        if (event.keyCode == 40) { 
         vm.$broadcast('down-arrow-pressed');
        }

        if (document.activeElement.tagName.toUpperCase() == 'INPUT' || document.activeElement.tagName.toUpperCase() == 'TEXTAREA') {
          return
        } else {
          console.log(document.activeElement.tagName)
        }        
        console.log(event.keyCode);
      });
    },
    methods: 
    {    

  	  squack: function(text)
      {
    	  alert(text)
      },

      redirect(parameter) 
      {
        this.$router.push(parameter);
      },
      async TeclaPresionadaIdentificacion(tecla) 
      {



        if (tecla == -1)
        {
          this.NumeroDocumento= '';
          return;
        }
        if (tecla == 99) 
        {





            // var config = {
            //     headers: {'Access-Control-Allow-Origin': '*'}
            // };
            // axios.get('http://10.17.2.33/matrix/webapi/tunero/obtenernombreusuario.php?tipoDocumento=CC&numeroIdentificacion=98591033', config)
            //     .then(function (response) {
            //         console.log(response.data);
            // });


          //axios.get('http://10.17.2.33/matrix/webapi/turnero/obtenernombreusuario.php?tipoDocumento=CC&numeroIdentificacion=98591033').then(function (response) {console.log(response.data);this.paciente =response.data; console.log(this.paciente);});
          axios.get('../obtenernombreusuario.php?tipoDocumento='+this.$store.state.Turno.TipoIdentificacion +'&numeroIdentificacion='+this.NumeroDocumento).then((response) => {

            //console.log(response.data);
            this.paciente =response.data; 
           //console.log(this.paciente);
            if (this.paciente.Nombre != null)
            {
              this.NombreCompleto =this.paciente.Nombre;
              this.Edad =this.paciente.Edad;


              //Ingreso por pasos

              //this.MostrarTecladoIdentificacion = false;
              //this.IdentifiacionSeleccionado = true;
              //this.NombreSeleccionado  = true;
              //this.EdadSeleccionado  = true;
              //this.MostrarTecladoNombre = false;
              //return;                            

            }
            else
            {
              //this.MostrarTecladoIdentificacion = false;
              //this.IdentifiacionSeleccionado = true;

              //Ingreso por pasos
              //this.MostrarTecladoNombre = true;
            }

          });
          
          
          this.MostrarTecladoIdentificacion = false;
          this.IdentifiacionSeleccionado = true;

          //Ingreso por pasos
          //this.MostrarTecladoNombre = true;




          return;
        }
        
        
        this.NumeroDocumento= this.NumeroDocumento + tecla;

      
      },
      TeclaPresionadaNombre(tecla) 
      {



        if (tecla == 99) 
        {
          this.MostrarTecladoNombre = false;
          this.NombreSeleccionado  = true;

          //Ingreso por pasos
          //this.MostrarTecladoEdad = true;

          return;
        }
        if (tecla == 0)
        {
          this.NombreCompleto= this.NombreCompleto.substring(0, this.NombreCompleto.length-1); 
          return;
        }

       if (tecla == 9)
        {
          this.NombreCompleto= this.NombreCompleto + ' '; 
          return;
        }
             
        this.NombreCompleto= this.NombreCompleto + tecla;
        this.ActivarInconsistencia='off';
      
      },

     TeclaPresionadaEdad(tecla) 
      {

        if (tecla == -1)
        {
          this.Edad= '';
          return;
        }

        if (tecla == 99) 
        {
          this.MostrarTecladoEdad = false;
          this.EdadSeleccionado  = true;
          return;
        }

        this.Edad= this.Edad + tecla;
        //this.TipoEdad= tipoEdad;

      
      },      

   
      clickCategoria(categoria) 
      {
          console.log(categoria);
          this.$store.commit("seleccionarCategoria", categoria);          
          this.Categoria= categoria

          //Ingreso por pasos

          //this.CategoriaSeleccionado   = true;
      },

      clickTipoDocumento(tipoDocumento) 
      {
          console.log(tipoDocumento);
          this.$store.commit("seleccionarTipoDocumento", tipoDocumento);
          this.TipoDocumento= tipoDocumento;

          //Ingreso por pasos
          //this.TipoIdentifiacionSeleccionado=true;
          //this.MostrarTecladoIdentificacion = true;
      },
     
    

      Terminar() 
      {
        this.redirect(`/`); 
      },

      async Continuar() 
      {
   
        if (this.TipoDocumento == '')
        {
            this.MensajeError = this.$store.state.ConfigTurnero.MensajeSinTipoDocumento;
            return;
        }


        if (this.NumeroDocumento == '')
        {
            this.MensajeError = this.$store.state.ConfigTurnero.MensajeSinNumeroDocumento;
            return;
        }

        if (this.NombreCompleto == '')
        {
            this.MensajeError = this.$store.state.ConfigTurnero.MensajeSinNombre;
            return;
        }

        if (this.Edad == '')
        {
            this.MensajeError = this.$store.state.ConfigTurnero.MensajeSinEdad;
            return;
        }

         if (this.Categoria == '')
        {
            this.MensajeError = this.$store.state.ConfigTurnero.MensajeSinCategoria;
            return;
        }

        var numEdad = parseInt(this.Edad)
        console.log('edad: ' + numEdad);
        console.log('TipoDocumento'+ this.TipoDocumento.Codigo);

        if (numEdad < 18  && this.TipoDocumento.Codigo == 'CC')
        {
            this.MensajeError = 'La edad debe ser mayor igual a 18 aos';
            return;
        }

        if (numEdad > 17  && this.TipoDocumento.Codigo == 'TI')
        {
            this.MensajeError = 'La edad debe menor de 18 aos';
            return;
        }

        this.$store.commit("changeTurnoNumeroIdentificacion", this.NumeroDocumento);
        this.$store.commit("changeTurnoNombre", this.NombreCompleto);
        this.$store.commit("changeTurnoEdad",  this.Edad);
        //is.$store.commit("changeTurnoTipoEdad", this.TipoEdad);                        
        this.$store.commit("changeTurnoTurno", 'SIN TURNO');         

        this.redirect(`/SeleccionPrioridad`); 
      },

      ClickEnCajaDeTexto(CajaDeTexto) 
      {
          if (CajaDeTexto == 'IDENTIFICACION')
          {
            this.MostrarTecladoIdentificacion = true;
            this.MostrarTecladoNombre = false;   
            this.MostrarTecladoEdad = false;   

            this.ColorIdentificacion = "";
            this.ColorNombre = "white";
            this.ColorEdad = "white";
          }

          if (CajaDeTexto == 'NOMBRE')
          {
            this.MostrarTecladoIdentificacion = false;
            this.MostrarTecladoNombre = true;   
            this.MostrarTecladoEdad = false;   

            this.ColorIdentificacion = "white";
            this.ColorNombre = "";
            this.ColorEdad = "white";

          }          
           
          
          if (CajaDeTexto == 'EDAD')
          {
            this.MostrarTecladoIdentificacion = false;
            this.MostrarTecladoNombre = false;   
            this.MostrarTecladoEdad = true;   

            this.ColorIdentificacion = "white";
            this.ColorNombre = "white";
            this.ColorEdad = "";

          }   

      }
    },
  }

  
</script>

