<template>
  <div class="ingresoIdentificacion">
   
    
    <!-- Encabezado -->
    <v-row  align="center" justify="center">
  
      
      <v-col cols="12">  
        <v-card  dark color="primary" height="120px"   class="mx-auto bl-xl" elevation="0" >
          <v-row  align="center" justify="center">
