    <!-- Formulario -->        
    <v-card loight color="white"  flat class="div-2" >
        <v-row  justify="center">

          <!-- Tipos de documento --> 
          <v-col   cols="5">  

                <div style="height:40px;"></div>
                <v-card light  color="white" flat  tile      class="d-flex align-content-space-around pt-5 pb-5 justify-space-around flex-wrap">             
            
                
        
                    <div v-for="tipoDocumento in ConfigTurnero.TiposDocumento" v-bind:key="tipoDocumento.Codigo" >

                      <v-btn  :height="CalcularAltoBoton(tipoDocumento.Nombre)" width="380"   elevation="8"   plain  :color="tipoDocumento.Color" :class="tipoDocumento.Clase"  rounded  v-on:click="clickTipoDocumento(tipoDocumento)">
                        <p class='textoaunapequenoIngresoIdentificacion' v-html="tipoDocumento.Nombre "></p>
        
                      </v-btn>
                      <div style="height:10px;"></div>
                    </div>

                </v-card>
        
          </v-col>

          <!-- Identificacin y teclado --> 
          <v-col  cols="7">     
             
            <v-card light justify="center" color="white" class=" pa-4" flat tile height="80%"  >    

              <v-row  justify="center">
                <v-col  cols="10">
                  <v-text-field color="primary" background-color="white" light   rounded  filled   outlined placeholder="Identificacin" v-model="NumeroDocumento" class="textoaunaIngresoIdentificacion text-primary pa-0 "  ></v-text-field>        
                </v-col>

              </v-row>
              <v-row  justify="center">
                <v-col  cols="10">
                  <div>
                    <TecladoNumericoPorPasos  @onKeyPress="TeclaPresionadaIdentificacion"></TecladoNumericoPorPasos>
