<script>
  import axios from 'axios';
  import TecladoNumericoPorPasos from "@/components/TecladoNumericoPorPasos.vue";

  export default 
  {
    components:
    {
      TecladoNumericoPorPasos
    },
   
    data () {
        return {
          NumeroDocumento: '',
          NombreCompleto:'',
          ErrorIngresoDatos: false,
          MensajeError: "Sin error",

        }
    },

    mounted: function() 
    {
      console.log("route:");
      console.log(this.$route);
  
      this.$store.commit("seleccionarTipoDocumento", "CC");

    },

    computed: {
      Turno () 
      {
        return this.$store.state.Turno;
      },
      ConfigTurnero () 
      {
        return this.$store.state.ConfigTurnero;
      }
    },
    methods: 
    {    
      redirect(parameter) 
      {
        this.$router.push(parameter);
      },
      continuar() 
      {

        if (this.$store.state.ConfigTurnero.TieneNombre == true && this.NombreCompleto == '')
        {
          this.redirect(`/IngresoNombre`); 
          return;
        }

        if (this.$store.state.ConfigTurnero.TieneEdad == true && this.NombreCompleto == '')
        {
          this.redirect(`/IngresoEdad`); 
          return;
        }

        if (this.$store.state.ConfigTurnero.TieneCategorias == true)
        {
          this.redirect(`/SeleccionCategoria`); 
          return;
        }

        if (this.$store.state.ConfigTurnero.TienePrioridad == true)
        {
          this.redirect(`/SeleccionPrioridad`); 
          return;
        }

        this.redirect(`/GenerarTurno`); 
 
      },

      CalcularAltoBoton(strTextoBoton) 
      {
        if (strTextoBoton.length > 20)
          return 80;
        else
          return 80;
      },

      clickTipoDocumento(tipoDocumento) 
      {
          console.log(tipoDocumento);
          this.$store.commit("seleccionarTipoDocumento", tipoDocumento);
          this.TipoDocumento = tipoDocumento;

          //Ingreso por pasos
          //this.TipoIdentifiacionSeleccionado=true;
          //this.MostrarTecladoIdentificacion = true;
      },
     async TeclaPresionadaIdentificacion(tecla) 
      {
        if (tecla == -1)
        {
          this.NumeroDocumento= '';
          return;
        }
        if (tecla == 99) 
        {
 
          if (this.NumeroDocumento.trim() == "")
          {
              this.ErrorIngresoDatos=true;
              this.MensajeError = this.$store.state.ConfigTurnero.MensajeSinNumeroDocumento;
              return;
          }
          this.$store.commit("changeTurnoNumeroIdentificacion", this.NumeroDocumento);
          axios.get('../obtenernombreusuario.php?tipoDocumento='+this.$store.state.Turno.TipoIdentificacion +'&numeroIdentificacion='+this.NumeroDocumento).then((response) => 
          {

 

            this.paciente =response.data; 
           //console.log(this.paciente);
            if (this.paciente.Nombre != null)
            {
              this.NombreCompleto =this.paciente.Nombre;
              this.Edad =this.paciente.Edad;
              this.$store.commit("changeTurnoNombre", this.paciente.Nombre);
              this.$store.commit("changeTurnoEdad",  this.paciente.Edad);                              
               
            }

          });
          
          this.continuar();
          return;
        }
        
        
        this.NumeroDocumento= this.NumeroDocumento + tecla;

      
      },
    },
  }

  
</script>

