          <v-col   cols="5">  

                <div style="height:40px;"></div>
                <v-card light  color="white" flat  tile      class="d-flex align-content-space-around pt-5 pb-5 justify-space-around flex-wrap">             
                    <div>
                      <v-btn  height="80" width="380"   elevation="2"  :class="ClaseAnos" rounded  v-on:click="clickTipoEdad('A')">
