<script>
  import axios from 'axios';
  import TecladoNumericoPorPasos from "@/components/TecladoNumericoPorPasos.vue";

  export default 
  {
    components:
    {
      TecladoNumericoPorPasos
    },
   
    data () {
        return {
          Edad: '',
          ErrorIngresoDatos: false,
          MensajeError: "Sin error",
          TextoAnos:"A&ntilde;os",
          TextoMeses:"Meses",
          TextoDias:"D&iacute;as",

          ClaseAnos: "m-botonSeleccionadoEdad",
          ClaseMeses: "m-botonEdad",
          ClaseDias: "m-botonEdad",


        }
    },

    mounted: function() 
    {
 
 
      console.log("route:");
      console.log(this.$route);
      this.$store.commit("changeTurnoTipoEdad", "A"); 
 
      console.log('Validacion edad: ' + this.$store.state.Turno.Edad + '');
      if (this.$store.state.Turno.Nombre + '' != '')
      {
        this.Edad =this.$store.state.Turno.Edad;
        console.log('Edad: ' + this.Edad);
        return;
      } 

    },

    computed: {
      Turno () 
      {
        return this.$store.state.Turno;
      },
      ConfigTurnero () 
      {
        return this.$store.state.ConfigTurnero;
      }
    },
    methods: 
    {    
      redirect(parameter) 
      {
        this.$router.push(parameter);
      },
      continuar() 
      {

        if (this.$store.state.ConfigTurnero.TieneCategorias == true)
        {
          this.redirect(`/SeleccionCategoria`); 
          return;
        }

        if (this.$store.state.ConfigTurnero.TienePrioridad == true)
        {
          this.redirect(`/SeleccionPrioridad`); 
          return;
        }

        this.redirect(`/GenerarTurno`); 
 
      },



      clickTipoEdad(tipoEdad) 
      {
          this.ClaseAnos='m-botonEdad';
          this.ClaseMeses='m-botonEdad';
          this.ClaseDias='m-botonEdad';
          
          if (tipoEdad == 'A')
          {
              this.ClaseAnos='m-botonSeleccionadoEdad';
          }
        
          if (tipoEdad == 'M')
          {
              this.ClaseMeses='m-botonSeleccionadoEdad';
          }

          if (tipoEdad == 'D')
          {
              this.ClaseDias='m-botonSeleccionadoEdad';
          }
          this.$store.commit("changeTurnoTipoEdad", tipoEdad); 
          console.log(this.$store.state.Turno);

      },
      TeclaPresionadaEdad(tecla) 
      {
        if (tecla == -1)
        {
          this.Edad= '';
          return;
        }
        if (tecla == 99) 
        {
 
          if (this.Edad == "")
          {
              this.ErrorIngresoDatos=true;
              this.MensajeError = this.$store.state.ConfigTurnero.MensajeSinEdad;
              return;
          }
          this.$store.commit("changeTurnoEdad", this.Edad);

          
          this.continuar();
          return;
        }
        
        
        this.Edad= this.Edad + tecla;

      
      },
    },
  }

  
</script>

