<script>
// @ is an alias to /src

export default 
{
  created() 
  {

  },
  data () {
      return {
        NumeroDocumento: '',

      }
  },
  methods: 
  {    
    redirect(parameter) 
    {
      this.$router.push(parameter);
    },
    TeclaPresionada(numero) 
    {
      if (numero == 99)
      {
        this.redirect(`/IngresoNombre`);
        return;
      }
      if (numero == -1)
      {
        this.NumeroDocumento="";
        return;
      }
      this.NumeroDocumento= this.NumeroDocumento + numero;
     
    }
  },
}
