<script>
  export default 
  {

    name: 'TecladoEdad',
    data () 
    {
        return {

          ColorAnos:'white',
          ColorMeses:'white',
          ColorDias:'white',

          DarkAnos: false,
          DarkMeses: false,
          DarkDias: false,


        }
    },
    methods: 
    {    
      redirect(parameter) 
      {
        this.$router.push(parameter);
      },
      TeclaPresionada(tecla) 
      {
        this.$emit("onKeyPress", tecla);
      },
      TipoEdadPresionada(tipoEdad) 
      {
          this.ColorAnos='white';
          this.ColorMeses='white';
          this.ColorDias='white';

          this.DarkAnos= false;
          this.DarkMeses= false;
          this.DarkDias= false;
          if (tipoEdad == 'A')
          {
              this.ColorAnos='black';
              this.DarkAnos= true;
          }
        
          if (tipoEdad == 'M')
          {
              this.ColorMeses='black';
              this.DarkMeses= true;
          }

          if (tipoEdad == 'D')
          {
              this.ColorDias='black';
              this.DarkDias= true;
          }
          this.$store.commit("changeTurnoTipoEdad", tipoEdad); 
          console.log(this.$store.state.Turno);

      }
    },
    computed: {
      Turno () 
      {
        return this.$store.state.Turno;
      },
      ConfigTurnero () 
      {
        return this.$store.state.ConfigTurnero;
      }
    },

    mounted: function() 
