<script>


  export default 
  {
  
    data () {
        return {
          ErrorIngresoDatos: false,
          MensajeError: "Sin error",

        }
    },
    computed: {
      Turno () 
      {
        return this.$store.state.Turno;
      },
      ConfigTurnero () 
      {
        return this.$store.state.ConfigTurnero;
      }
    },
    methods: 
    {    
      redirect(parameter) 
      {
        this.$router.push(parameter);
      },
      continuar() 
      {
        if (this.$store.state.ConfigTurnero.TieneSubCategorias == true)
        {
          if (this.$store.state.Turno.Subcategorias.length > 0)
  	      {        
            this.redirect(`/SeleccionSubcategoria`); 
            return;
          }
        }

        if (this.$store.state.ConfigTurnero.TienePrioridad == true)
        {
          this.redirect(`/SeleccionPrioridad`); 
          return;
        }

        this.redirect(`/GenerarTurno`); 
 
      },

      clickCategoria(Categoria) 
      {
          console.log(Categoria);
          this.$store.commit("seleccionarCategoria", Categoria);
          this.continuar();

      },
  
    },
  }

  
</script>

