<script>

  import axios from 'axios';
  export default 
  {
    data () 
    {
        return{
          TextoTurno: 'Por favor tome su tiquete, en un momento sera atendido',
          turnoGenerado: false,
        }
    },

    computed: 
    {
      Turno () 
      {
        return this.$store.state.Turno;
      },
      ConfigTurnero () 
      {
        return this.$store.state.ConfigTurnero;
      }
    },

   mounted: function() 
    {
  
      //this.$store.commit("changeTurnoPrioridad", this.Prioridad);
      console.log("Genearar turno, Turno:");
      console.log(this.$store.state.Turno);
      this.TextoTurno = this.ConfigTurnero.MensajeTurnoGenerado;
      var bodyFormData = new FormData();
      bodyFormData.append('tipoDocumento', this.$store.state.Turno.TipoIdentificacion);
      bodyFormData.append('numeroIdentificacion', this.$store.state.Turno.NumeroIdentificacion);
      bodyFormData.append('nombrePaciente', this.$store.state.Turno.Nombre);
      bodyFormData.append('edad', this.$store.state.Turno.Edad);
      bodyFormData.append('tipoEdad', this.$store.state.Turno.TipoEdad);
      bodyFormData.append('categoria',this.$store.state.Turno.Categoria);
      bodyFormData.append('subcategoria', this.$store.state.Turno.Subcategoria);      
      bodyFormData.append('prioridad',this.$store.state.Turno.Prioridad);
      bodyFormData.append('codigoTurnero', this.$store.state.ConfigTurnero.CodigoTurnero);
      bodyFormData.append('tema', this.$store.state.ConfigTurnero.CodigoTurnero);
      bodyFormData.append('solucionCitas', this.$store.state.ConfigTurnero.SolucionCitas);

      if (this.$store.state.ConfigTurnero.EsUrgencias == true)
      {
          bodyFormData.append('tipoTurnero', 'URGENCIAS');
      }else{
        if (this.$store.state.ConfigTurnero.TieneCitas == true)
        {
            bodyFormData.append('tipoTurnero', 'ENDOSCOPIA');
        }else{

          bodyFormData.append('tipoTurnero', 'LOBBY');   
        }
      }

      bodyFormData.append('wemp_pmla', '01');
      bodyFormData.append('validarExisteTurno',this.$store.state.ConfigTurnero.ValidarExisteTurno);

      axios.post("../generarturno.php", bodyFormData,
      {
        headers: 
        {'Content-Type': 'multipart/form-data'}
      }).then((response) => 
      {
          //console.log(response.data);
          this.turno =response.data; 
          //console.log(this.turno);
          this.turno.Turno = this.turno.Turno.substring(7);
          //this.turno.Turno =  this.turno.Turno.slice(this.turno.Turno.length-7,this.turno.Turno.length);
          this.$store.dispatch("changeTurno", this.turno);
          console.log(this.$store.state.Turno);
          this.turnoGenerado = true;
         

      });
      //this.timer = setInterval(this.Terminar, 4000);
  
    },    

    methods: 
    {    
      redirect(parameter) 
      {
        this.$router.push(parameter);
      },
      Terminar() 
      {
        this.ImprimirTurno();
        this.$router.push({path:'/',query:{wemp_pmla: this.$store.state.ConfigTurnero.wemp_pmla,tema:this.$store.state.ConfigTurnero.CodigoTurnero,portal:this.$store.state.ConfigPortal.CodigoPortal }}).catch(()=>{});
        //this.redirect(`/`); 
      },


      ImprimirTurno() 
      {
        var Reimpresion = false;
        var html = "";

        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();
        today = yyyy + '/' + mm + '/' + dd;

        var strTime  = new Date(); 
        var hours = strTime.getHours();
        var minutes = strTime.getMinutes();
        var ampm = hours >= 12 ? 'pm' : 'am';
        hours = hours % 12;
        hours = hours ? hours : 12; // the hour '0' should be '12'
        minutes = minutes < 10 ? '0'+minutes : minutes;
        var strTime = hours + ':' + minutes + ' ' + ampm;
  

        html = html + "<table style='font-family: verdana;font-size:1rem;'>";
        html = html + "	<tr>";
        html = html + "		<td colspan='2' align='center'>";
        html = html + "			<img width='118' heigth='58' src='../../../images/medical/root/cliame.jpg'>";
        html = html + "		</td>";
        html = html + "	</tr>";
        html = html + "	<tr>";
        html = html + "		<td colspan='2' align='center'>";
        html = html + "			Es un placer servirle.";
        html = html + "			<br><br>";
        html = html + "		</td>";
        html = html + "	</tr>";
        html = html + "	<tr>"
        html = html + "		<td >Turno:&nbsp;&nbsp;</td>";
        html = html + "		<td align='right' style='font-size:2rem;'><b>" + this.$store.state.Turno.Turno +"</b></td></tr>";
        html = html + "	<tr>";
        html = html + "		<td style='padding-bottom:3px;' colspan='2'>" + this.$store.state.Turno.TipoIdentificacion +"&nbsp;&nbsp;&nbsp;" +  this.$store.state.Turno.NumeroIdentificacion + "</td>";
        html = html + "	</tr>";
        html = html + "	<tr>";
        html = html + "		<td style='padding-bottom:3px;' colspan='2'>" + this.$store.state.Turno.Nombre + "</td>";
        html = html + "	</tr>";
        html = html + "	<tr>";
        html = html + "		<td colspan='2' align='center' style='font-size:0.8rem'>";
        html = html + "			<br><b>Por favor conserve este tiquete hasta la consulta con el m&eacute;dico.</b>";
        html = html + "		</td>";
        html = html + "	</tr>";
        html = html + "	<tr>";
        html = html + "		<td colspan='2' align='center' style='font-size:0.7rem'>";
        if (Reimpresion)
          html = html + "			<b>(Reimpresi&oacute;n)</b> Fecha: " + today + " &nbsp;Hora: " + strTime;
        else
          html = html + "			Fecha: " + today + " &nbsp;Hora: " + strTime;
        html = html + "		</td>";
        html = html + "	</tr>";
        html = html + "	<tr>";
        html = html + "	<br>";
      html = html + "	<br>";      
        html = html + "		<td colspan='2' align='center' style='font-size:0.7rem'>";
        html = html + "		  <b>Unidad destino:</b><br><br> _____________________________";
        html = html + "	<br>";
        html = html + "	<br>";      
        html = html + "		  <b>Turno:</b> _____________";
        html = html + "		</td>";
        html = html + "	</tr>";      
        html = html + "</table>";

        var contenido	= "<html><body onload='window.print();window.close();'>";
        contenido 		= contenido + html + "</body></html>";

        var windowAttr = "location=yes,statusbar=no,directories=no,menubar=no,titlebar=no,toolbar=no,dependent=no,width=1,height=1,resizable=yes,screenX=1,screenY=1,personalbar=no,scrollbars=no";
        var ventana = window.open( "", "",  windowAttr );
        ventana.document.write(contenido);
        ventana.document.close();

      },

    },   

  }

  
</script>

