    // console.log(this.$store.state.ConfigTurnero);  
    // console.log(this.$store.state.Turno);
    // this.$store.commit("changeTurnoNumeroIdentificacion", "prueba");   
 
    var vm = this;


    window.addEventListener('keyup', function(event) 
    {
      
      
      //vm.$store.commit("changeTurnoNumeroIdentificacion", "prueba");
      //console.log(event.keyCode);
      //console.log(event.key);     
      if((event.keyCode >= 48 && event.keyCode <= 90) || event.key == "Tab" || event.key == "Enter")
      {
        switch(event.key)
        {
          case "Tab":
          {					
            vm.textoLector = vm.textoLector +"|";
            return false;
          }
          case "Enter":
          {
            // --> 	Despues de un enter, procedo a validar que sea una lectura desde una cedula o TI
            //		Condiciones: Que sea una array de 13 elementos y que el primero elemento se tipo numerico
            //vm.textoLector ="Lectura Completa";
            //alert(vm.$store.state.Turno.NumeroIdentificacion);
            var arrayTextoLector 	= vm.textoLector.split('|');
            //alert('Tamao:' + arrayTextoLector.length);
            //if((arrayTextoLector.length == 12 || arrayTextoLector.length == 13)  && Number.isInteger(arrayTextoLector[0]*1))
            if((arrayTextoLector.length == 12 || arrayTextoLector.length == 13))

            {
              //alert('1');
              var primerCaracterDelsegundoTab = arrayTextoLector[1].substr(0,1);
              //alert('2');
              var fechaNacimiento
              // --> Si el primer caracter del segundo tab es un entero, indica que es una tarjeta de identidad
              if(Number.isInteger(primerCaracterDelsegundoTab*1))
              {
                vm.$store.commit("changeTurnoNumeroIdentificacion", String.trim(arrayTextoLector[0]+primerCaracterDelsegundoTab));               
                //alert('3');
                var segundoApellido = trim(arrayTextoLector[1].substr(1,arrayTextoLector[1].length));
                vm.$store.commit("changeTurnoNombre", trim(arrayTextoLector[3])+" "+ trim(arrayTextoLector[4])+" "+segundoApellido+" "+String.trim(arrayTextoLector[2]));
                var ano 			= arrayTextoLector[6].substr(1,arrayTextoLector[6].length)+arrayTextoLector[7].substr(0,1);
                var mes 			= arrayTextoLector[7].substr(1,1)+arrayTextoLector[8].substr(0,1);
                var dia 			= arrayTextoLector[8].substr(1,1)+arrayTextoLector[9].substr(0,1);							
                fechaNacimiento = String.trim(ano+"-"+mes+"-"+dia);
              }
              // --> Sino, es una cedula
              else
              {
                let strNombre = arrayTextoLector[3]+" "+arrayTextoLector[4]+" "+arrayTextoLector[1]+" "+arrayTextoLector[2];
                strNombre = strNombre.toUpperCase()
                vm.$store.commit("changeTurnoNumeroIdentificacion",(arrayTextoLector[0]*1));						
                vm.$store.commit("changeTurnoNombre", strNombre);					
                
                vm.$store.commit("seleccionarTipoDocumento", "CC");
                //alert(vm.textoLector);
                vm.textoLector = '';
                //alert('3');
                let fechaNacimiento = arrayTextoLector[6]+"-"+arrayTextoLector[7]+"-"+arrayTextoLector[8];
                //alert("FecNac:"+ fechaNacimiento);  
                
                var hoy = new Date();
                var cumpleanos = new Date(fechaNacimiento);
                var edad = hoy.getFullYear() - cumpleanos.getFullYear();
                var m = hoy.getMonth() - cumpleanos.getMonth();

                if (m < 0 || (m === 0 && hoy.getDate() < cumpleanos.getDate())) {
                    edad--;
                }

                vm.$store.commit("changeTurnoEdad", edad);
                vm.SeleccionIngresoAutomatico();
               
              }
              vm.textoLector = '';
              //alert('CC:'+vm.$store.state.Turno.NumeroIdentificacion);
              //alert('Nombre:'+vm.$store.state.Turno.Nombre);
              
            }
            else
            {
              //alert('Formato invalido' + arrayTextoLector.length + '-'+ arrayTextoLector[0]);
              //reiniciarPantalla();
              //$("#divMensaje").html("Formato invalido, solo se permiten cedulas o tarjetas de identidad.");						
            }
            
            vm.textoLector = '';
            event.cancelBubble 	= true;
            event.returnValue 	= false;
            return false;
            
          }
          default:
          {
            vm.textoLector = vm.textoLector + event.key;
            //console.log(vm.texoLector);
            break;
          }
        }
        
      }
      else
      {
        //return;
        event.cancelBubble 	= true;
        event.returnValue 	= false;
        return false;
      }
 
    });

 

  },
  methods: 
  {    

    redirect(parameter) 
    {
      
      //this.$router.push({name:'IngresoIdentificacion',params:{Pid:'prueba'},query: { plan: 'private' }});
      this.$router.push(parameter);
    },
    
    LlamarTurno() 
