      //this.$router.push({name:'IngresoIdentificacion',params:{Pid:'prueba'},query: { plan: 'private' }});
      this.LlamadoTurno = true;
    },


    SeleccionIngresoAutomatico() 
    {
      
      if (this.$store.state.ConfigTurnero.IngresoPorPasos == true)
      {
        if (this.$store.state.ConfigTurnero.TieneCategorias == true)
        {
            this.redirect(`/IngresoCategoria`);
            return;
        }

        if (this.$store.state.ConfigTurnero.TienePrioridad == true)
        {
            this.redirect(`/IngresoPrioridad`);
            return;
        }

        this.redirect(`/GenerarTurno`);

      }else{
        this.redirect(`/IngresoPrioridad`);        
      }

    },
   
    SeleccionIngresoManual() 
    {
      
      if (this.$store.state.ConfigTurnero.IngresoPorPasos == true)
      {
        if (this.$store.state.ConfigTurnero.TieneTipoDocumento == true)
        {
            this.redirect(`/IngresoIdentificacion`);
            return;
        }
        if (this.$store.state.ConfigTurnero.TieneNombre == true)
        {
            this.redirect(`/IngresoNombre`);
            return;
        }
        if (this.$store.state.ConfigTurnero.TieneEdad == true)
        {
            this.redirect(`/IngresoEdad`);
            return;
        }
        if (this.$store.state.ConfigTurnero.TieneCategorias == true)
        {
            this.redirect(`/IngresoCategoria`);
            return;
        }

        if (this.$store.state.ConfigTurnero.TienePrioridad == true)
        {
            this.redirect(`/IngresoPrioridad`);
            return;
        }

        this.redirect(`/GenerarTurno`);

      }else{
        this.redirect(`/IngresoNombreModelo`);        
      }

    },
